<template>
  <div class="info" >
    <van-pull-refresh v-model="isLoading" @refresh="onRefresh" success-text="刷新成功">
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      class="list">
    <!-- <SignUp></SignUp> -->

    <div class="showComment " v-for="(item,index) in list"
    :key="list.info_id" @click="toRead(item.info_article_id)">
        <!-- 头像-->
        <UserTag :msgg="item.user_info" class="headPor"></UserTag>
        <!-- <HeadPor class="headPor"
        :msgg="{ authen:item.comment.user_authen, img:item.comment.user_img,name:item.comment.user_name,user_lv:item.comment.user_lv,gold:item.comment.user_gold}"></HeadPor> -->
        <!-- 下部 -->
        <div class="bottom">
          <!-- 时间-->
          <div>
            <i class="iconfont time">&#xe607;</i>
            <span class="spanOne">{{item.info_time|fTime}}</span>
          </div>
          <!-- 几楼 -->
          <div>
            <span v-if='item.user_info.comment_index'>
                {{item.user_info.comment_index}}楼
            </span>
          </div>
        </div>
        <!-- 如果是回复评论的评论 @click="toRead(item.info_article_id)"......................................-->
        <div v-if="item.info_is_reply_comment" >
          <!-- 原来的评论-->
          <div class="replyComment" >
            <div>回复您的评论</div>
            <!-- 被回复的评论内容 class="re_content "-->
            <div :class="['re_content',!item.user_info.replyTemp.comment_content?'del':'']">
              {{item.user_info.replyTemp.comment_content|fNoComment}}
            </div>
          </div>
          <!-- 评论 class="commentContent"-->
          <div :class="['commentContent',!item.user_info.comment_content?'del':'']">
            {{item.user_info.comment_content|fNoComment}}
          </div>
        </div>
        <!-- 如果回复文章的评论 .....@click="toRead(item.info_article_id)"..........................................-->
        <div v-if="item.info_is_reply_article" >
          <!-- 原来的评论-->
          <div class="replyComment" >
            <div>回复您的文章</div>
            <!-- 被回复的评论内容 class="re_content"-->
            <div :class="['re_content',!item.article_title?'del':'']">
              {{item.article_title|fNoArticle}}
            </div>
          </div>
          <!-- 评论 class="commentContent"-->
          <div :class="['commentContent',!item.user_info.comment_content?'del':'']">
            {{item.user_info.comment_content|fNoComment}}
          </div>
        </div>
        <!-- 如果是别人打赏的消息.......@click="toRead(item.info_article_id)"................................. -->
        <div v-if="item.info_is_reward" >
          <!-- 原来的评论-->
          <div class="replyComment" >
            <div>土豪打赏您的文章</div>
            <!-- 被回复的评论内容 class="re_content"-->
            <div :class="['re_content',!item.article_title?'del':'']">
              {{item.article_title|fNoArticle}}
            </div>
          </div>
          <!-- 评论 -->
          <div class="commentContentForGold">
            打赏您的文章<i class="iconfont gold">&#xe601;</i> : {{item.info_reward_num}}颗
          </div>
        </div>
        <!-- 结束////////////////////////////////////////////-->


    </div>

  </van-list>

  </van-pull-refresh>
  </div>
</template>

<script>
  /* 引入头像组件*/
  // import HeadPor from "@/components/utils/HeadPor"
  import UserTag from "@/components/utils/UserTag"
	/* 引入导航栏组件*/
	// import SignUp from "@/components/me/utils/SignUp"

 import {createNamespacedHelpers} from 'vuex'
 const list=createNamespacedHelpers('list')
 const login=createNamespacedHelpers('login')
  export default{
    name:"",
    activated(){
      /* 如果有未读消息*/
      // console.log(this.userInfo.user_unread,'this.userInfo.user_unread')
      if(this.userInfo.user_unread > 0){
        //缓冲显示
        this.isBuffering = true
        // 清空list
        // this.list = []
        this.list.length = 0
        this.onLoad()
        // this.finished = false
        /* 清空未读消息*/
        this.setHandler(['user_unread','jian'])
      }
      else if(this.firstEnter > 1 && (
       this.fromPath != '/read'
      // && this.fromPath != '/folAndFans'
      && this.fromPath != '/search')){

        /* 如果不是在第一次进入并且... 则更新数据*/
         /* 下面针对含有list的组件*/
        //缓冲显示
        this.isBuffering = true
        // 清空list
        // this.list = []
        this.list.length = 0
        this.onLoad()
      }else{
        this.firstEnter ++
      }
    },
    /* 数据*/
    data(){
      return {
          firstEnter: 0,
          list : [],
          loading: false,
          finished: false,
          isLoading: false,
      }
    },
    filters:{

    },
    /* 方法*/
    methods:{
      mountedFn(){
        this.firstEnter ++
        //缓冲显示
        this.isBuffering = true
      },
      /* 修改用户信息方法*/
      ...login.mapMutations({
              setHandler: 'setHandler'
      }),
      // 得到文章列表
      getArticle(params,num){
        /* ***********************************************************************************/
        // this.$api.user[`${this.info[num]}`](params).then(
        this.$api.user.info(params).then(
            res =>{
              console.log(res.data,'用户消息数据')
              //缓冲消失
              this.isBuffering = false
              // 刷新状态结束
              this.isLoading = false
               // 加载状态结束
               this.loading = false;
               //文章触发加载
               this.finished = false
               // if(res.data =="no_list_login"){
               //   //文章触发加载
               //   this.finished = true
               //   this.$dialog.confirm({
               //             title: '您尚未登录',
               //             message: '需要跳转登录页吗?'
               //           }).then(() => {
               //             // on 点击确定 去登录页
               //             this.$router.push({
               //               path : '/login'
               //             })
               //           }).catch(() => {
               //             // on cancel
               //           });

               // }else
              if(res.data =="no_data" ){
                //文章不再触发加载
                this.finished = true
              }else if(res.data.finished){
                //如果后端告知无需刷新
                this.finished = true
                this.getFn(res.data.result,num)
              }else if(res.data.ok){
                this.getFn(res.data.result,num)
              }else{
                this.finished = true
              }
            }
        )
        /* ***********************************************************************************/
      },
      getFn(arg,num){
        console.log(arg,'argargargargargargargargargargarg')
        // let tempArg = []
           for(let i =0;i<arg.length;i++){
             arg[i].info_time = this.$formatDate(new Date(arg[i].info_time))
             if(arg[i].user_info_not_found){
               /* 如果评论已经被删除*/
               arg[i].user_info = {
                 replyTemp :{}
               }
              // tempArg.push(arg[i])
             }else{
               // tempArg.push(arg[i])
             }

           }
          /* 数字*/
           switch(num){
            case 0:
                this.list = arg
                break;
            case 1:
                this.list = this.list.concat(arg)
                break;
              default:
           }
      },
      onRefresh(){
        // //缓冲显示
        // this.isBuffering = true
        // this.getArticle({},0)
        this.list.length = 0
        this.onLoad()
      },

      toRead(params){
        this.$router.push({
          path: '/read',
          query : {
            articleId : params
          }
        })
      },
      onLoad() {
        // alert("onload")

        if(!!this.list[0] == true){
          var params = {
            time : this.list[this.list.length-1].info_time,
          }
          this.getArticle(params,1)
        }else{
          this.getArticle({},0)
        }
      },
      // formatDate(date){
      //   var y = date.getFullYear();
      //       var m = date.getMonth() + 1;
      //       m = m < 10 ? ('0' + m) : m;
      //       var d = date.getDate();
      //       d = d < 10 ? ('0' + d) : d;
      //       var h = date.getHours();
      //       var minute = date.getMinutes();
      //       minute = minute < 10 ? ('0' + minute) : minute;
      //       var second= date.getSeconds();
      //       second = second < 10 ? ('0' + second) : second;
      //       var Milliseconds= date.getMilliseconds();
      //       if(Milliseconds < 10){
      //         Milliseconds =  ('00' + Milliseconds)
      //       }
      //       if(Milliseconds < 100){
      //         Milliseconds =  ('0' + Milliseconds)
      //       }
      //       // second = second < 10 ? ('0' + second) : second;
      //       return y + '-' + m + '-' + d+' '+h+':'+minute+':'+ second+'.'+ Milliseconds;
      // }
      // formatDate(date){
      //   var y = date.getFullYear();
      //       var m = date.getMonth() + 1;
      //       m = m < 10 ? ('0' + m) : m;
      //       var d = date.getDate();
      //       d = d < 10 ? ('0' + d) : d;
      //       var h = date.getHours();
      //       var minute = date.getMinutes();
      //       minute = minute < 10 ? ('0' + minute) : minute;
      //       var second= date.getSeconds();
      //       second = second < 10 ? ('0' + second) : second;
      //       return y + '-' + m + '-' + d+' '+h+':'+minute+':'+ second;
      // }
    },
    /* 计算*/
    computed:{
      // 用户信息
      ...login.mapState({
           	userInfo : state => state.userInfo
           }),
      fromPath:{
        get () {
              return this.$store._modulesNamespaceMap['path/'].context.state.fromPath
        },
        set (value) {
          this.$store._modulesNamespaceMap['path/'].context.commit('fromPathHandler', value)
        }
      },
      isBuffering:{
        get () {
              return this.$store._modulesNamespaceMap['win/'].context.state.isBuffering
        },
        set (value) {
          this.$store._modulesNamespaceMap['win/'].context.commit('isBufferingHandler', value)
        }
      },
     // ...nav.mapState({
     //      	code : state => state.code
     //      })
	 ...list.mapState({
	      	info : state => state.info
	      }),
    },
    // 组件
    components : {
    	/* 这个组件名字不能和原生标签名重复 */
      // SignUp
      // HeadPor
      UserTag
    },
    /* 挂载阶段*/
    mounted() {
      this.mountedFn()
      // this.loadingForHttpHandler(true)
        // this.$api.user.info({}).then(
        //     res =>{
        //       console.log(res.data,"用户通知消息")
        //       this.list = res.data
        //     }
        // )

    },
    // 监听
    watch:{
      // '$route.path': {
      //     handler(newVal, oldVal) {
      //       console.log(newVal)
      //       switch(newVal){
      //         case '/':
      //             this.infoHandler(['info','infoMore'])
      //             break;
      //       }
      //     },
      //     immediate: true,
      //     // deep: true
      // }
    }
  }
</script>

<style scoped lang="scss">
  .info{
    background-color: white !important;
    @include wh(655px,auto);
    padding-bottom: 100px;

    .showComment{
      border-bottom: 10px solid $jsBgcGray;
      // background-color: white;
      padding :  15px;
            // 头像//////////////////
      .headPor{
        // margin-bottom: -25px;
      }
      // 被回复内容
      .replyComment{
        background-color: rgb(240,240,240);
        margin-left: 60px;
        padding-left: 20px;
        color: #a0a0a0;
        margin-bottom: 20px;
        font-size: 16px;
        line-height: 24px;
        div{
          padding:5px 0;
        }
        div:nth-child(1){
          color:rgb(190,190,190);
        }
        div:nth-child(2){
          color:rgb(104,104,104);
        }
      }
      // 评论内容
      .commentContent{
        color:rgb(70,70,70);
        font-size:16px;
        line-height: 24px;
        word-break: break-all;
        margin-left: 60px;
      }
      .commentContentForGold{
        color: $red;
        font-size:18px;
        word-break: break-all;
        margin-left: 60px;
        .gold{
          font-size: 18px;
        }
      }
      // 下部
      .bottom{
        padding-bottom: 10px;
        @include flex(row,space-between ,center)
        // margin-left: 120px;
        height: 18px;
        line-height: 18px;
        div:nth-child(1){
          color:#ccc;
          span{
            font-size: 14px;
          }
          .time{
            font-size: 14px;
            margin-left: 60px;
          }
        }
        div:nth-child(2){
          span{
            color:$green;
            font-size: 14px;
          }
        }
        /* 更多*/

      }
    }
  }
  // @import "../../assets/scss/com/List.scss";
  // 单个评论
  .del{
  	color:$jsHomeBottom !important;
  }
</style>



